import { WindowCreator } from "../WindowCreator";

const FileMenu = {
    name: "",
    children: {
        "file-new": {
            name: " ",
            children: {},
            shortcut: ["ctrl", "n"],
            action: function(ev: any) {
                if (window.app) {
                    window.app.emit("openWindow", {
                        path: "/newWindow"
                    });
                }
            }
        },
        "file-open": {
            name: " ",
            shortcut: ["ctrl", "o"],
            children: {}
        },
        "file-close": {
            name: " ",
            children: {}
        },
        "file-save": {
            name: " ",
            children: {}
        },
        "file-preferences": {
            name: " ",
            children: {}
        },
        "file-export": {
            name: "",
            children: {}
        },
        "file-exit": {
            name: " ",
            children: {},
            action: function(ev: any) {
                const { ipcRenderer } = require("electron");
                ipcRenderer.send("exit");
            }
        }
    }
};

export { FileMenu };
