import { WindowCreator } from "../WindowCreator";
import { ElectronService } from "../ElectronService";

const GameMenu = {
    name: "",
    children: {
        "game-playtest": {
            name: " ",
            children: {},
            action: (ev: any) => {
                alert("    .");
            },
        },
        "game-fullscreen": {
            name: " ",
            children: {},
            action: (ev: any) => {},
        },
        "game-show-console": {
            name: " ",
            children: {},
            action: (ev: any) => {},
        },
        "game-folder-open": {
            name: "  ",
            children: {},
            action: (ev: any) => {
                // @ts-ignore
                if (platform === "electron") {
                    const service = new ElectronService();
                    if (process.platform === "darwin") {
                        service.openFolder(__dirname);
                    } else {
                        service.openFolder(location.href);
                    }
                }
            },
        },
    },
};

export { GameMenu };
